.editable-cell {
    position: relative;
    border: 1px solid #d9d9d9;
  }
  
  .editable-cell-value-wrap {
    padding: 5px 12px;
    cursor: pointer;
    border: 1px solid #d9d9d9;
  }
  
  .editable-row:hover .editable-cell-value-wrap {
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    padding: 4px 11px;
  }